/* Hylove 自訂主題 - 大地禪風 */
@theme {
  /* 主色調 - 溫暖大地色 */
  --color-primary: #78716c;      /* stone-500 - 主按鈕 */
  --color-primary-hover: #57534e; /* stone-600 */
  --color-primary-light: #f5f5f4; /* stone-100 */

  /* 強調色 - 柔和琥珀 */
  --color-accent: #b45309;        /* amber-700 */
  --color-accent-light: #fef3c7;  /* amber-100 */
  --color-accent-muted: #d97706;  /* amber-600 */

  /* 背景色 */
  --color-bg: #fafaf9;            /* stone-50 */
  --color-bg-card: #ffffff;
  --color-bg-sidebar: #ffffff;
  --color-bg-hover: #f5f5f4;      /* stone-100 */
  --color-bg-active: #e7e5e4;     /* stone-200 */

  /* 文字色 */
  --color-text: #292524;          /* stone-800 */
  --color-text-secondary: #57534e; /* stone-600 */
  --color-text-muted: #78716c;    /* stone-500 */
  --color-text-light: #a8a29e;    /* stone-400 */

  /* 邊框色 */
  --color-border: #e7e5e4;        /* stone-200 */
  --color-border-light: #f5f5f4;  /* stone-100 */

  /* 狀態色 */
  --color-success: #65a30d;       /* lime-600 */
  --color-success-light: #ecfccb; /* lime-100 */
  --color-error: #dc2626;         /* red-600 */
  --color-error-light: #fee2e2;   /* red-100 */
}

/* DaisyUI 主題覆寫 - 大地禪風 */
:root {
  /* Primary - 石色 */
  --p: 78 113 108;                /* stone-500 HSL converted to oklch base */
  --pc: 255 255 255;              /* primary content - white */

  /* Secondary - 暖灰 */
  --s: 87 83 78;                  /* stone-600 */
  --sc: 255 255 255;

  /* Accent - 琥珀 */
  --a: 180 83 9;                  /* amber-700 */
  --ac: 255 255 255;

  /* Neutral - 深石色 */
  --n: 41 37 36;                  /* stone-800 */
  --nc: 255 255 255;

  /* Base colors */
  --b1: 255 255 255;              /* base-100 white */
  --b2: 250 250 249;              /* base-200 stone-50 */
  --b3: 245 245 244;              /* base-300 stone-100 */
  --bc: 41 37 36;                 /* base-content stone-800 */

  /* State colors */
  --su: 101 163 13;               /* success lime-600 */
  --suc: 255 255 255;
  --wa: 180 83 9;                 /* warning amber-700 */
  --wac: 255 255 255;
  --er: 185 28 28;                /* error red-700 */
  --erc: 255 255 255;
  --in: 87 83 78;                 /* info stone-600 */
  --inc: 255 255 255;
}

/* DaisyUI 按鈕樣式覆寫 */
.btn-primary {
  background-color: #57534e !important;  /* stone-600 */
  border-color: #57534e !important;
  color: white !important;
}
.btn-primary:hover {
  background-color: #44403c !important;  /* stone-700 */
  border-color: #44403c !important;
}

.btn-secondary {
  background-color: #78716c !important;  /* stone-500 */
  border-color: #78716c !important;
  color: white !important;
}
.btn-secondary:hover {
  background-color: #57534e !important;  /* stone-600 */
  border-color: #57534e !important;
}

.btn-accent {
  background-color: #92400e !important;  /* amber-800 */
  border-color: #92400e !important;
  color: white !important;
}
.btn-accent:hover {
  background-color: #78350f !important;  /* amber-900 */
  border-color: #78350f !important;
}

.btn-error {
  background-color: #b91c1c !important;  /* red-700 */
  border-color: #b91c1c !important;
  color: white !important;
}
.btn-error:hover {
  background-color: #991b1b !important;  /* red-800 */
  border-color: #991b1b !important;
}

.btn-error.btn-outline {
  background-color: transparent !important;
  border-color: #b91c1c !important;
  color: #b91c1c !important;
}
.btn-error.btn-outline:hover {
  background-color: #b91c1c !important;
  color: white !important;
}

.btn-success {
  background-color: #4d7c0f !important;  /* lime-700 */
  border-color: #4d7c0f !important;
  color: white !important;
}
.btn-success:hover {
  background-color: #3f6212 !important;  /* lime-800 */
  border-color: #3f6212 !important;
}

.btn-ghost {
  color: #57534e !important;  /* stone-600 */
}
.btn-ghost:hover {
  background-color: #e7e5e4 !important;  /* stone-200 */
}

.btn-outline {
  border-color: #a8a29e !important;  /* stone-400 */
  color: #57534e !important;  /* stone-600 */
}
.btn-outline:hover {
  background-color: #57534e !important;  /* stone-600 */
  border-color: #57534e !important;
  color: white !important;
}

/* Badge 樣式覆寫 */
.badge-primary {
  background-color: #57534e !important;  /* stone-600 */
  border-color: #57534e !important;
  color: white !important;
}

.badge-secondary {
  background-color: #78716c !important;  /* stone-500 */
  border-color: #78716c !important;
  color: white !important;
}

.badge-accent {
  background-color: #92400e !important;  /* amber-800 */
  border-color: #92400e !important;
  color: white !important;
}

.badge-success {
  background-color: #4d7c0f !important;  /* lime-700 */
  border-color: #4d7c0f !important;
  color: white !important;
}

.badge-error {
  background-color: #b91c1c !important;  /* red-700 */
  border-color: #b91c1c !important;
  color: white !important;
}

.badge-info {
  background-color: #57534e !important;  /* stone-600 */
  border-color: #57534e !important;
  color: white !important;
}

.badge-warning {
  background-color: #92400e !important;  /* amber-800 */
  border-color: #92400e !important;
  color: white !important;
}

.badge-outline {
  background-color: transparent !important;
  border-color: #a8a29e !important;  /* stone-400 */
  color: #57534e !important;  /* stone-600 */
}

/* Alert 樣式覆寫 */
.alert-info {
  background-color: #e7e5e4 !important;  /* stone-200 */
  border-color: #a8a29e !important;  /* stone-400 */
  color: #292524 !important;  /* stone-800 */
}

.alert-warning {
  background-color: #fef3c7 !important;  /* amber-100 */
  border-color: #d97706 !important;  /* amber-600 */
  color: #78350f !important;  /* amber-900 */
}

.alert-success {
  background-color: #ecfccb !important;  /* lime-100 */
  border-color: #65a30d !important;  /* lime-600 */
  color: #365314 !important;  /* lime-900 */
}

.alert-error {
  background-color: #fee2e2 !important;  /* red-100 */
  border-color: #dc2626 !important;  /* red-600 */
  color: #7f1d1d !important;  /* red-900 */
}

/* Steps 樣式覆寫 */
.step-primary::after,
.step-primary + .step-primary::before {
  background-color: #57534e !important;  /* stone-600 */
  color: white !important;
}

/* Tab 樣式覆寫 */
.tabs-boxed {
  background-color: #e7e5e4 !important;  /* stone-200 */
}

.tab-active {
  background-color: #57534e !important;  /* stone-600 */
  color: white !important;
}

/* Menu active 樣式 */
.menu li > a.active {
  background-color: #e7e5e4 !important;  /* stone-200 */
  color: #292524 !important;  /* stone-800 */
}

/* Checkbox 樣式覆寫 */
.checkbox:checked {
  background-color: #57534e !important;  /* stone-600 */
  border-color: #57534e !important;
}

/* File input 樣式覆寫 */
.file-input::file-selector-button {
  background-color: #e7e5e4 !important;  /* stone-200 */
  border-color: #a8a29e !important;  /* stone-400 */
  color: #292524 !important;  /* stone-800 */
}

/* Ring/Focus 樣式 */
.ring-primary {
  --tw-ring-color: #78716c !important;  /* stone-500 */
}

/* Link 樣式覆寫 */
.link-primary {
  color: #57534e !important;  /* stone-600 */
}
.link-primary:hover {
  color: #44403c !important;  /* stone-700 */
}

/* Tailwind bg-primary 覆寫（用於 Logo 等） */
.bg-primary {
  background-color: #57534e !important;  /* stone-600 */
}

/* Steps 樣式覆寫（教學頁步驟圓圈） */
.steps .step-primary + .step-primary:before,
.steps .step-primary:after {
  background-color: #57534e !important;  /* stone-600 */
}

.step::before {
  background-color: #a8a29e !important;  /* stone-400 for inactive */
}

.step-primary::before {
  background-color: #57534e !important;  /* stone-600 */
}

/* Steps 數字圓圈 */
.step::after {
  background-color: #a8a29e !important;  /* stone-400 */
  color: white !important;
}

.step-primary::after {
  background-color: #57534e !important;  /* stone-600 */
  color: white !important;
}

/* 基礎樣式 */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--color-bg);
  color: var(--color-text);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
